<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-employee-educational-histories',
  data () {
    return {
      config: {
        title: 'Riwayat Pendidikan Tenaga Kerja',
        model_api: 'employee-educational-histories',
        getter: 'employee-educational-histories',
        setter: 'employee-educational-histories',
        pk_field: null,
        permission: {
          create: 'create-employee-educational-histories',
          read: 'view-employee-educational-histories',
          update: 'update-employee-educational-histories',
          delete: 'delete-employee-educational-histories'
        },
        slave: [],
        fields: [
          { id: 'employee_id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'institution', label: 'Nama Institusi' },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'start_date', label: 'Tanggal Mulai', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date' }, update: { type: 'date' }, filter: false } },
          { id: 'end_date', label: 'Tanggal Selesai', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date' }, update: { type: 'date' }, filter: false } },
          {
            id: 'education_id',
            label: 'Jenjang Pendidikan',
            methods: {
              create: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-educations',
                getter: 'master-educations',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          }
          // {
          //   id: 'employee_id',
          //   label: 'Nama Tenaga Kerja',
          //   methods: {
          //     create: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     },
          //     update: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     },
          //     filter: {
          //       setter: 'employees',
          //       getter: 'employees',
          //       type: 'lookup-radio',
          //       option: {
          //         list_pointer: {
          //           label: 'fullname', code: 'id', display: ['nip', 'fullname']
          //         }
          //       }
          //     }
          //   }
          // }
        ]
      }
    }
  }
}
</script>



